body {
  --black: #000;
  --white: #fff;
  --VSCodeBG: #11151a;
  --BGColor: #282828;
  --BGColor1: #262d41;
  --BGColor2: #313a50;
  --SkyBlue: #61dafb;
  --Cyans: #21ebff;
  --DarkCyans: #17a9b3;
  --Blue: #2456ff;
  --LightBlue: #69a5f8;
  --YelGreen: #21ff30;
  --Green: #17b322;
}

.big {
  color: #0ff;
  font-size: 25px;
}

/* 입력하는 부분 ---------------- */
.top_input {
  display: flex;
  justify-content: center;
}

.top_input>input {
  height: 30px;
  margin-right: 10px;
}

#inputBody { width: 300px; }
#inputDate { height: 32px; }
#inputSubmit { height: 34px; }

#inputTitle, #inputDate, #inputBody {
  color: var(--white);
  background-color: var(--VSCodeBG);
  outline: none;
  border: 1px solid var(--LightBlue);
  transition: 1s;
}

#inputTitle:hover, #inputDate:hover, #inputBody:hover {
  transition: 10ms;
  border: 1px solid var(--YelGreen);
}

#inputTitle:focus, #inputDate:focus, #inputBody:focus {
  outline: none !important;
  border: 1px solid var(--Cyans);
  box-shadow: 0 0 20px var(--Cyans);
  transition: all 0.5s ease-in-out;
}

#inputSubmit {
  border: 1px solid var(--LightBlue);
  background-color: var(--VSCodeBG);
  color: var(--white);
  font-size: 15px;
  letter-spacing: 2px;
  width: 50px;
  margin-bottom: 1px;
  transition: 1s;
}

#inputSubmit:hover {
  background-color: var(--YelGreen);
  border-color: var(--YelGreen);
  color: #000;
  border-radius: 0px;
  transition: all 0.5s;
}

#inputSubmit:active {
  background-color: var(--Cyans);
  color: #000;
  border-radius: 0px;
  box-shadow: 0 0 2px #21ebff55, 0 0 4px var(--Cyans), 0 0 8px var(--Cyans), 0 0 16px var(--Cyans);
  transition: 0s;
}

/* 보이는 영역 -------------- */
.headHR {
  margin-left: 40px;
  margin-right: 40px;
  border: 0;
  height: 2px;
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.1));
}

.specialHR {
  margin-left: 40px;
  margin-right: 40px;
  border: 0;
  border-radius: 10px;
  height: 5px;
  background-image: linear-gradient(to right, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 1));
}

.container-dday {
  margin-left: 40px;
  min-width: 500px;
  height: 35px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.delbtn-container { margin-left: -10px; }

.delbtn {
  border: none;
  outline: none;
  background-color: var(--BGColor1);
  color: var(--white);
  text-align: center;
  align-items: center;
  width: 45px;
  height: 35px;
  font-size: 16px;
  display: flex;
  justify-content: space-between;
  transition: 1s;
}

.delbtn:hover {
  background-color: var(--Cyans);
  color: #000;
  box-shadow: 0 0 2px #21ebff55, 0 0 4px #21ebff, 0 0 8px #21ebff, 0 0 16px #21ebff;
  transition: all 0.5s;
}

.item-dday:nth-child(1) {
  display: flex;
  justify-content: center;
  flex-basis: 70px;
}
.item-dday:nth-child(2) { flex-basis: 100px; }
.item-dday:nth-child(3) { flex-basis: 110px; }
.item-dday:nth-child(4) { flex-basis: 100px; }
.item-dday:nth-child(5) { flex-basis: 300px; }