body {
  --black: #000;
  --white: #fff;
  --VSCodeBG: #11151a;
  --BGColor: #282828;
  --BGColor1: #262d41;
  --BGColor2: #313a50;
  --SkyBlue: #61dafb;
  --Cyans: #21ebff;
  --DarkCyans: #17a9b3;
  --Blue: #2456ff;
  --LightBlue: #69a5f8;
  --YelGreen: #21ff30;
  --Green: #17b322;
}

body { --inputHeight: 30px; }

/* input area */
#title, #date, #body {
  color: var(--white);
  background-color: var(--VSCodeBG);
  outline: none;
  border: 1px solid var(--LightBlue);
  transition: 1s;
}
#title {
  width: 300px;
  height: var(--inputHeight)
}
#date {
  margin-left: 9px;
  width: 190px;
  height: var(--inputHeight)
}
#body {
  width: 500px;
  height: 100px;
}
#title:hover, #date:hover, #body:hover {
  transition: 10ms;
  border: 1px solid var(--YelGreen);
}
#title:focus, #date:focus, #body:focus {
  outline: none !important;
  border: 1px solid var(--Cyans);
  box-shadow: 0 0 20px var(--Cyans);
  transition: all 0.5s ease-in-out;
}

.diary-submit {
  padding: 10px 20px;
  color: var(--white);
  background-color: var(--VSCodeBG);
  font-size: 16px;
  letter-spacing: 4px;
  border: 1px solid var(--LightBlue);
  margin: auto;
  transition: 1s;
}
.diary-submit:hover {
  background-color: var(--YelGreen);
  border-color: var(--YelGreen);
  color: #000;
  border-radius: 0px;
  transition: all 0.5s;
}
.diary-submit:active {
  background-color: var(--Cyans);
  color: #000;
  border-radius: 0px;
  box-shadow: 0 0 2px #21ebff55, 0 0 4px var(--Cyans), 0 0 8px var(--Cyans), 0 0 16px var(--Cyans);
  transition: 0s;
}

/* 컨텐츠 보이는 부분 */
.diary-container {
  display: flex;
  flex-direction: column;
  margin: 20px;
  align-items: center;
}

body { --paddingVal: 20px }

.diary-box {
  border: 1px solid;
  border-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.1));
  border-image-slice: 1;
  margin: var(--paddingVal);
  padding: var(--paddingVal);
  width: 650px;
  transition: all 1s;
}
.diary-box:hover {
  border: 1px solid var(--YelGreen);
  transition: all 0.5;
}
.diary-box:active {
  border: 1px solid var(--Cyans);
  box-shadow: 0 0 2px #21ebff55, 0 0 4px #21ebff, 0 0 8px #21ebff, 0 0 16px #21ebff;
  transition: all 0s;
}

.diary-box>hr {
  margin-left: 30px;
  margin-right: 30px;
  border: 0;
  height: 2px;
  border-radius: 5px;
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 1), rgba(255, 255, 255, 0.1));
}

#btn {
  padding: 5px 10px;
  color: var(--white);
  background-color: var(--BGColor);
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.3s;
  letter-spacing: 4px;
  border: 1px solid var(--LightBlue);
  margin: 10px;
}
#btn:hover {
  color: var(--black);
  border: 1px solid var(--Cyans);
  background-color: var(--Cyans);
  box-shadow: 0 0 3px #21ebff55, 0 0 9px #21ebff, 0 0 18px #21ebff, 0 0 27px #21ebff;
}

.diary-body-date {
  margin: 20px;
  font-size: 0.75rem;
  text-align: right;
  margin-right: 2rem;
}

.diary-body-title {
  margin: 20px;
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
}

.diary-body-text {
  margin: 20px;
  text-align: center;
}